import { useEffect } from 'react';

export const useImperativeDisableScroll = (element: any, disabled: any) => {
  useEffect(() => {
    if (!element) {
      return;
    }
    element.style.overflowY = disabled ? 'hidden' : 'scroll';
    return () => {
      element.style.overflowY = 'scroll';
    };
  }, [disabled, element]);
};
