import * as action from '../../resources/constants';
//import { contract } from '../../web3/contract-api';

export const set_shows = (value: string) => ({
  type: action.SET_LANGUAGE,
  value,
});

export const get_contract = (value: any) => ({ type: action.GET_CONTRACT, value });

export const get_contract_thunk = (contr: any) => async (dispatch: (arg0: { type: string; value: any }) => void) => {
  const current_contract = contr;
  dispatch(get_contract(current_contract));
};
