import React, { FC } from 'react';
import styles from './coming_soon.module.scss';
import Tree from '../../../assets/images/Bild.png';
import { useTranslation } from 'react-i18next';

export const ComingSoon: FC = () => {
  const { t } = useTranslation();

  return (
    <section className={styles.wrapper}>
      <img src={Tree} className={styles.background} alt="background" />
      {/* <div id="soon" className={styles.soon}></div> */}
      <div className={styles.textWrapper}>
        <p className={styles.text}>{t('coming-soon')}</p>
      </div>
    </section>
  );
};
