/* eslint-disable no-useless-concat */
import { FC } from 'react';
import styles from './header.module.scss';
import Particles from 'react-particles-js';
import { Link, NavLink } from 'react-router-dom';
import { HeaderLeftPanelLinks } from '../../../resources/link.enum';

import { useTranslation } from 'react-i18next';
import Logo from '../../../assets/images/logo.svg';
import GreenImage from '../../../assets/images/green_image.png';
//import StakingImage from '../../../assets/images/Bild_rotated.png';
import StakingImage from '../../../assets/images/Bild_rotated.png';
import termsImage from '../../../assets/images/new_images/03_Terms.jpg';
import CoinImage from '../../../assets/images/new_images/04_Staking.jpg';
import { RoutingPaths } from '../../../resources/routing_paths.enum';
import { particles_params } from '../../../resources/particles.params';

import cn from 'classnames';
import { useTranslationHook } from '../../../hooks/translation.hook';
import { useImperativeDisableScroll } from '../../../hooks/useImperativeDisableScroll.hook';
import { useHeaderLogic } from '../hooks/useHeader.hook';

export const Header: FC = () => {
  const { t } = useTranslation();
  const { pathname, routsPaths, isMenuOpen, selectedLanguage, setLanguage, scrolling, menuStateHandler, setMenuState } =
    useHeaderLogic();
  useTranslationHook();
  useImperativeDisableScroll(document.scrollingElement, isMenuOpen);

  return (
    <section className={styles.wrapper}>
      <div className={styles.motto}>
        <p className={styles.upperText}>{t('header-motto.upper-text')}</p>
        <p className={styles.bottomText}>{t('header-motto.bottom-text')}</p>
      </div>
      <div
        className={cn(
          styles.mobileMenuButton,
          { [styles.mobileMenuButtonFixed]: isMenuOpen },
          { [styles.opened]: isMenuOpen },
        )}
        onClick={menuStateHandler}
      >
        <div className={styles.burgerWrapper}>
          <div className={cn(styles.burgerTop, { [styles.rotatedTop]: isMenuOpen })}></div>
          <div className={styles.burgerCenter} style={isMenuOpen ? { backgroundColor: 'transparent' } : {}}></div>
          <div className={cn(styles.burgerBottom, { [styles.rotatedBottom]: isMenuOpen })}></div>
        </div>
      </div>
      {isMenuOpen && <div className={styles.overlay}></div>}
      {isMenuOpen && (
        <div className={styles.mobileMenu}>
          <ul>
            <li>
              <NavLink exact to={RoutingPaths.HOME} activeClassName={styles.selectedMobile}>
                {t('navigation-menu.home')}
              </NavLink>
            </li>
            <li>
              <NavLink exact to={RoutingPaths.STAKING} activeClassName={styles.selectedMobile}>
                {t('navigation-menu.staking')}
              </NavLink>
            </li>
            <li onClick={() => setMenuState(false)}>
              <NavLink exact to={RoutingPaths.HOME}>
                {t('navigation-menu.liquidity-mobile')}
              </NavLink>
            </li>
            <li></li>
            <li onClick={menuStateHandler}>
              <Link to={RoutingPaths.IMPRINT} className={styles.link}>
                {t('navigation-menu.mobile-menu-imprint')}
              </Link>
            </li>
            <li onClick={menuStateHandler}>
              <Link to={RoutingPaths.PRIVACY} className={styles.link}>
                {t('navigation-menu.mobile-menu-protection')}
              </Link>
            </li>
            <li onClick={menuStateHandler}>
              <Link to={RoutingPaths.TERMS} className={styles.link}>
                {t('navigation-menu.mobile-menu-terms')}
              </Link>
            </li>
          </ul>
          <div className={styles.languageToggle}>
            <p
              className={cn(styles.lang, { [styles.langCurrent]: selectedLanguage.language === 'Deutsch' })}
              onClick={() => setLanguage('Deutsch')}
            >
              Deutsch
            </p>
            <p
              className={cn(styles.lang, { [styles.langCurrent]: selectedLanguage.language === 'English' })}
              onClick={() => setLanguage('English')}
            >
              English
            </p>
          </div>
        </div>
      )}
      <div className={styles.tsparticlesContainer}>
        <Particles params={particles_params} />
      </div>
      {pathname === RoutingPaths.HOME && (
        <>
          <img src={GreenImage} alt="img" className={styles.greenImage} />
          {!isMenuOpen && (
            <div
              className={styles.scrollButton}
              onClick={() => {
                document.getElementById('lp_info')!.scrollIntoView({ block: 'start', behavior: 'smooth' });
              }}
            >
              <p>{t('start-button')}</p>
            </div>
          )}
        </>
      )}

      {pathname !== RoutingPaths.HOME && (
        <>
          {pathname === RoutingPaths.STAKING && <img src={CoinImage} alt="img" className={styles.coinImage} />}
          {pathname !== RoutingPaths.STAKING && <img src={termsImage} alt="img" className={styles.coinImage} />}
          <img src={StakingImage} alt="img" className={styles.StakingImage} />
          <div
            className={cn(styles.upperLine, {
              [styles.hiddenMobile]:
                pathname === RoutingPaths.PRIVACY ||
                pathname === RoutingPaths.TERMS ||
                pathname === RoutingPaths.IMPRINT,
            })}
          ></div>
          <div
            className={cn(styles.bottomLine, {
              [styles.hiddenMobile]:
                pathname === RoutingPaths.PRIVACY ||
                pathname === RoutingPaths.TERMS ||
                pathname === RoutingPaths.IMPRINT,
            })}
          ></div>{' '}
          <div className={styles.textBlock}>
            {pathname === RoutingPaths.STAKING && (
              <>
                <p className={styles.title}>{t('staking-page.title')}</p>
                <p className={styles.mainText}>{t('staking-page.main-text')}</p>
              </>
            )}
            {routsPaths.includes(pathname) && (
              <>
                <p className={styles.title} style={{ textTransform: 'capitalize' }}>
                  {pathname === RoutingPaths.PRIVACY
                    ? t('navigation-menu.mobile-menu-protection')
                    : pathname === RoutingPaths.TERMS
                    ? t('navigation-menu.mobile-menu-terms')
                    : t('navigation-menu.mobile-menu-imprint')}
                </p>
                <p className={styles.mainText}>{t('headerText.agreements.address')}</p>
                <p className={styles.mainText} style={{ marginBottom: 0 }}>
                  {t('headerText.agreements.role')}
                </p>
                <p className={styles.mainText} style={{ marginTop: 0 }}>
                  {t('headerText.agreements.name')}
                </p>
                <p className={styles.mainText}>{t('headerText.agreements.registration')}</p>
                <p className={styles.mainText}>{t('headerText.agreements.email')}</p>
              </>
            )}
          </div>
        </>
      )}
      <img src={Logo} className={styles.headerLogo} alt="logo" />
      <nav className={styles.navigation}>
        {/* <div className={styles.motto}>
          <p className={styles.upperText}>{t('header-motto.upper-text')}</p>
          <p className={styles.bottomText}>{t('header-motto.bottom-text')}</p>
        </div> */}
        <ul>
          <li>
            <NavLink exact to={RoutingPaths.HOME} activeClassName={styles.selected}>
              {t('navigation-menu.home')}
            </NavLink>
          </li>
          <li>
            <NavLink exact to={RoutingPaths.STAKING} activeClassName={styles.selected}>
              {t('navigation-menu.staking')}
            </NavLink>
          </li>
          <li
            onClick={() => {
              scrolling();
            }}
            id="liquidity"
          >
            <NavLink exact to={RoutingPaths.HOME}>
              {t('navigation-menu.liquidity')}
            </NavLink>
          </li>
        </ul>

        <select value={selectedLanguage.language} onChange={event => setLanguage(event.target.value)}>
          <option>Deutsch</option>
          <option>English</option>
        </select>
      </nav>
      <section className={styles.coloredBlock}>
        <div className={styles.leftBlock}>
          <div className={styles.line}></div>
          <p className={styles.visitUsText}>{t('left-panel')}</p>
          <a className={styles.a3} href={HeaderLeftPanelLinks.MONDO_GUIDE} rel="noreferrer" target="_blank">
            mondo.guide
          </a>
          <a className={styles.a2} href={HeaderLeftPanelLinks.MONDO_GATE} rel="noreferrer" target="_blank">
            mondo.gate
          </a>
          <a className={styles.a1} href={HeaderLeftPanelLinks.MONDO_COIN} rel="noreferrer" target="_blank">
            mondo.coin
          </a>
        </div>
      </section>
    </section>
  );
};
