import React, { FC } from 'react';
import styles from './lp_information.module.scss';
import { Link } from 'react-router-dom';
import Video from '../../../assets/video/test_video.mp4';
import Image from '../../../assets/images/new_images/06_Video.jpeg';
import { RoutingPaths } from '../../../resources/routing_paths.enum';
import { useTranslation } from 'react-i18next';
import Icon from '../../../assets/icons/Digital-nomad-working-near-beach-3-256.png';

export const LPInformation: FC = () => {
  const { t } = useTranslation();

  return (
    <section className={styles.wrapper}>
      <img src={Icon} className={styles.icon} alt="icon" />
      <div className={styles.topLine}></div>
      <div className={styles.bottomLine}></div>
      <div className={styles.hidden} id="lp_info"></div>
      <div className={styles.infoBlock}>
        <div className={styles.textBlock}>
          <p className={styles.title1}>{t('video-block.title1')}</p>
          <p className={styles.title2}>{t('video-block.title2')}</p>
          <p className={styles.mainText}>{t('video-block.main-text')}</p>
        </div>
        <div className={styles.videoBlock}>
          {/* <video width="100%" height="100%" loop autoPlay muted>
            <source src={Video} type="video/mp4" />
          </video> */}
          <img src={Image} alt="some_picture" />
        </div>
      </div>
      <div className={styles.buttonBlock}>
        <div className={styles.redirectButton}>
          <Link to={RoutingPaths.STAKING}>{t('start-button')}</Link>
        </div>
      </div>
    </section>
  );
};
