import { FC } from 'react';

import { Link } from 'react-router-dom';
import styles from './lp_pool_dashboard_card.module.scss';
import cn from 'classnames';

import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';

import Dash_Logo from '../../../assets/icons/dashboard_logo.svg';
import Beige_Icon from '../../../assets/icons/streamline-icon-accounting-coins-stack@25x25.svg';
import Gift_Icon from '../../../assets/icons/streamline-icon-gift-box@25x25.svg';
import APY_Icon from '../../../assets/icons/streamline-icon-performance-increase@25x25.svg';
import Green_Icon from '../../../assets/icons/streamline-icon-cash-payment-coin-stash@25x25.svg';
import Black_Icon from '../../../assets/icons/streamline-icon-database-check@25x25.svg';
import { RoutingPaths } from '../../../resources/routing_paths.enum';

import { useDashboard } from '../hooks/useDashboard.hook';

interface Props {
  staking_page?: boolean;
  popUpStateHandler?: (a: string) => void;
}

export const PoolDashboardCard: FC<Props> = ({ staking_page, popUpStateHandler }) => {
  const { build_styles, percentage, supplyNum, t } = useDashboard();

  return (
    <section
      className={cn(
        styles.wrapper,
        { [styles.smallBottomMargin]: staking_page },
        { [styles.narrow]: staking_page },
        { [styles.lowZ]: staking_page },
      )}
    >
      <p className={styles.dashNameVisible}>{t('pool-dashboard.pool-name')}</p>
      <div className={styles.upperBlock}>
        <p className={styles.dashName}>{t('pool-dashboard.pool-name')}</p>
        <img src={Dash_Logo} alt="icon" className={styles.icon} />
      </div>
      <div className={styles.bottomBlock}>
        <div className={styles.leftBlock}>
          <div className={styles.kpiWrapper}>
            <img src={APY_Icon} alt="icon" className={styles.kpiIcon} />
            <div className={styles.dataWrapper}>
              <p className={styles.title} style={{ textTransform: 'uppercase' }}>
                {t('pool-dashboard.apy')}
              </p>
              <p className={styles.data}>25.89%</p>
            </div>
          </div>
          <div className={styles.kpiWrapper}>
            <img src={Gift_Icon} alt="icon" className={styles.kpiIcon} />
            <div className={styles.dataWrapper}>
              <p className={styles.title}>{t('pool-dashboard.rewards-alle-title')}</p>
              <p className={styles.data}>{t('pool-dashboard.rewards-alle-text')}</p>
            </div>
          </div>
          <div className={styles.kpiWrapper}>
            <img src={Black_Icon} alt="icon" className={styles.kpiIcon} />
            <div className={styles.dataWrapper}>
              <p className={styles.title}>{t('pool-dashboard.access-title')}</p>
              <p className={styles.data}>{t('pool-dashboard.access-text')}</p>
            </div>
          </div>
        </div>
        <div className={styles.centralBlock}>
          <div className={styles.progress}>
            <CircularProgressbar value={percentage} text={`${percentage}%`} counterClockwise styles={build_styles} />
          </div>
          <div className={styles.progressDataContainer}>
            <div className={styles.kpiWrapper}>
              <img src={Green_Icon} alt="icon" className={styles.kpiIcon} />
              <div className={styles.dataWrapper}>
                <p className={styles.title} style={{ marginBottom: '0.5rem' }}>
                  {t('pool-dashboard.Verfügbare')}
                </p>
                <p className={styles.data} style={{ color: '#64a640' }}>
                  12.059.204 MNDCC
                </p>
              </div>
            </div>
            <div className={styles.kpiWrapper}>
              <img src={Beige_Icon} alt="icon" className={styles.kpiIcon} />
              <div className={styles.dataWrapper}>
                <p className={styles.title} style={{ marginBottom: '0.5rem' }}>
                  {t('pool-dashboard.Gesamtanzahl')}
                </p>
                <p className={styles.data} style={{ color: '#c6b5a4' }}>
                  {supplyNum.toFixed(0)} MNDCC
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className={styles.rightBlock}>
          {staking_page && (
            <div className={styles.hidden}>
              <div className={styles.scrollButton} onClick={() => popUpStateHandler!('claim')}>
                <p> {t('pool-dashboard.pull reward-button')}</p>
              </div>
              <div className={styles.scrollButton} onClick={() => popUpStateHandler!('remove')}>
                <p>{t('pool-dashboard.remove-button')}</p>
              </div>
              <div className={styles.scrollButton} onClick={() => popUpStateHandler!('buy')}>
                <p>{t('pool-dashboard.buy-button')}</p>
              </div>
            </div>
          )}
          {!staking_page && (
            <Link to={RoutingPaths.STAKING}>
              <div className={styles.scrollButton}>
                <p>{t('pool-dashboard.buy-button')}</p>
              </div>
            </Link>
          )}
        </div>
      </div>
    </section>
  );
};
