import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation } from 'react-router-dom';
import { set_shows } from '../../../redux/actions/actions';
import { RootState } from '../../../redux/store';
import { RoutingPaths } from '../../../resources/routing_paths.enum';

export const useHeaderLogic = () => {
  const [isMenuOpen, setMenuState] = useState<boolean>(false);

  console.log(isMenuOpen);

  const dispatch = useDispatch();
  const selectedLanguage = useSelector((state: RootState) => state.language);
  const menuStateHandler = () => {
    setMenuState(!isMenuOpen);
  };

  const setLanguage = (language: string) => {
    dispatch(set_shows(language));
  };

  const location = useLocation();
  const pathname = location.pathname;

  const scrolling = () => {
    setTimeout(() => {
      document.getElementById('soon') &&
        document.getElementById('soon')!.scrollIntoView({ block: 'start', behavior: 'smooth' });
    }, 10);
  };

  const routsPaths: string[] = [RoutingPaths.IMPRINT, RoutingPaths.PRIVACY, RoutingPaths.TERMS];
  return {
    routsPaths,
    isMenuOpen,
    selectedLanguage,
    pathname,
    setLanguage,
    menuStateHandler,
    scrolling,
    setMenuState,
  };
};
