import { GET_CONTRACT } from '../../resources/constants';

const initial_state = {
  current_contract: {},
};

interface Action {
  value: string;
  type: string;
}

export const get_contract_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case GET_CONTRACT:
      return {
        ...state,
        current_contract: action.value,
      };
    default:
      return state;
  }
};
