import { FC } from 'react';
import styles from './pop_up.module.scss';
import Logo from '../../assets/icons/dashboard_logo.svg';
import CashIcon from '../../assets/icons/streamline-icon-cash-payment-coin-stash@25x25.svg';
import Arrow from '../../assets/icons/icons-tabbar-share.svg';
import cn from 'classnames';
import { BASE_URL } from '../../resources/constants';
import { useTranslation } from 'react-i18next';

interface Props {
  isPopUpOpen: boolean;
  data: any;
  popUpType: string;
  closePopUpStateHandler: () => void;
  contractHash: string;
}

export const PendingPopUp: FC<Props> = ({ closePopUpStateHandler, popUpType, contractHash }) => {
  const { t } = useTranslation();
  return (
    <section className={styles.wrapper}>
      <div className={styles.pop_up}>
        <div className={styles.upperBlock}>
          <p className={styles.name}>{'Pool 1'}</p>
          <img src={Logo} alt="logo" className={styles.logo} />
          <div className={styles.cross} onClick={closePopUpStateHandler}>
            <div></div>
            <div></div>
          </div>
        </div>

        <div className={styles.dataBlock}>
          <img
            src={CashIcon}
            className={cn(styles.cashIcon, { [styles.cashIconBig]: popUpType === 'claim' })}
            alt="icon"
          />

          <div className={styles.leftPart}>
            <p className={styles.title}>{t('pop_up.transaction_pending')}</p>
          </div>
        </div>

        <div className={cn(styles.popUpNameWrapper, styles.popUpNameWrapperPending)}>
          <p className={styles.popUpName}>{t('pop_up.transaction_status')}</p>

          <p style={{ fontSize: '2.2rem', fontWeight: 200, width: '35rem' }}>{t('pop_up.transaction_check')}</p>
          <a href={`${BASE_URL}/${contractHash}`} target="blank" rel="noreferrer">
            <div className={styles.roundedButton}>
              <img src={Arrow} className={styles.arrow} alt="icon" />

              <p>{t('pop_up.status_button')}</p>
            </div>
          </a>
        </div>

        <div className={styles.popUpInputWrapper}></div>

        <div className={cn(styles.buttonBlock, styles.pendingPopText)}>
          <p style={{ fontSize: '1.4rem', fontWeight: 200, width: '30rem' }}>{t('pop_up.info')}</p>
          <div className={styles.button} style={{ marginRight: '1rem' }} onClick={closePopUpStateHandler}>
            {t('pop_up.dashboard_button')}
          </div>
        </div>
      </div>
    </section>
  );
};
