import { BigNumber, Contract, ethers } from 'ethers';

declare global {
  interface Window {
    ethereum: any;
  }
}

const stakingAbi = [
  'function balanceOf(address) view returns (uint)',
  'function totalSupply() view returns (uint)',
  'function earned(address) view returns (uint)',
  'function approve(address spender, uint amount)',
  'function stake(uint amount)',
  'function withdraw(uint amount)',
  'function claim()',
  'function exit()',
];

const mndccAbi = [
  'function name() view returns (string)',
  'function symbol() view returns (string)',
  'function balanceOf(address) view returns (uint)',
  'function totalBalanceOf(address) view returns (uint)',
  'function approve(address spender, uint amount)',
  'function transfer(address to, uint amount)',
  'function transferOwnership(address newOwner)',
  'function transferVested(address to, uint amount, uint startDate)',
  'event Transfer(address indexed from, address indexed to, uint amount)',
  'event Vesting(address indexed to, uint amount, uint startDate)',
];

type ChainId = number;

const contractAddress: Record<ChainId, string> = {
  31337: '0x123',
  3: '0x47e6e1B1C01BEF1f32Ff1f2A8858a262d0DBDA12',
  1: '0x789',
};

const mndccAddress: Record<ChainId, string> = {
  31337: '0x123',
  3: '0x8c8Ce13836f2B930e07594e4985303c986e08D26',
  1: '0xCDb9D30a3BA48CdfCB0EcbE19317E6cf783672f1',
};

async function connect() {
  if (typeof window.ethereum !== undefined) {
    await window.ethereum.enable();
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const chainId = (await provider.getNetwork()).chainId;
    const signer = provider.getSigner();

    const contract = new ethers.Contract(contractAddress[chainId], stakingAbi, signer);
    const mndcc = new ethers.Contract(mndccAddress[chainId], mndccAbi, signer);
    return { chainId, contract: implementContractApi(contract, mndcc) };
  } else {
    throw new Error('No injected web3 found');
  }
}

function implementContractApi(contract: Contract, mndcc: Contract) {
  async function balanceOf(): Promise<BigNumber> {
    return await contract.balanceOf(contract.signer.getAddress());
  }

  async function totalSupply(): Promise<BigNumber> {
    return await contract.totalSupply();
  }

  async function earned(): Promise<BigNumber> {
    return await contract.earned(contract.signer.getAddress());
  }

  async function approve(amountUnits: BigNumber): Promise<ethers.Transaction> {
    return await mndcc.approve(contract.address, amountUnits);
  }

  async function stake(amountUnits: BigNumber): Promise<ethers.Transaction> {
    return await contract.stake(amountUnits);
  }
  async function claim(): Promise<ethers.Transaction> {
    return await contract.claim();
  }
  async function withdraw(amountUnits: any): Promise<ethers.Transaction> {
    return await contract.withdraw(amountUnits);
  }
  async function exit(): Promise<ethers.Transaction> {
    return await contract.exit();
  }
  return {
    balanceOf,
    totalSupply,
    earned,
    approve,
    stake,
    claim,
    withdraw,
    exit,
  };
}

export const contract = {
  connect,
};
