import { FC } from 'react';
import styles from './pop_up.module.scss';
import Logo from '../../assets/icons/dashboard_logo.svg';
import CashIcon from '../../assets/icons/streamline-icon-cash-payment-coin-stash@25x25.svg';
import cn from 'classnames';
import { usePopUp } from './usePopUp.hook';
import { useTranslation } from 'react-i18next';

interface Props {
  isPopUpOpen: boolean;
  data: any;
  popUpType: string;
  closePopUpStateHandler: () => void;
  openPopUpPendingStateHandler: () => void;
  closePopUpPendingStateHandler: () => void;
  setHash: (arg: string) => void;
}

export const PopUp: FC<Props> = ({
  data,
  closePopUpStateHandler,
  openPopUpPendingStateHandler,
  closePopUpPendingStateHandler,
  setHash,
  popUpType,
}) => {
  const { t } = useTranslation();
  const { onChangeHandler, earnedCount, amount, onClickHandler } = usePopUp(
    data,
    setHash,
    closePopUpStateHandler,
    openPopUpPendingStateHandler,
    closePopUpPendingStateHandler,
  );

  return (
    <section className={styles.wrapper}>
      <div className={styles.pop_up}>
        <div className={styles.upperBlock}>
          <p className={styles.name}>{'Pool 1'}</p>
          <img src={Logo} alt="logo" className={styles.logo} />
          <div className={styles.cross} onClick={closePopUpStateHandler}>
            <div></div>
            <div></div>
          </div>
        </div>

        <div className={styles.dataBlock} style={popUpType === 'claim' ? { marginRight: '9rem' } : {}}>
          <img
            src={CashIcon}
            className={cn(styles.cashIcon, { [styles.cashIconBig]: popUpType === 'claim' })}
            alt="icon"
          />
          {popUpType !== 'claim' && (
            <div className={styles.leftPart}>
              <p className={styles.title}>{t('pop_up.leftPartTitle.not_claim')}</p>
              <p className={styles.data}>{'12.059.204 MNDCC'}</p>
            </div>
          )}

          {popUpType === 'claim' && (
            <div className={styles.leftPart} style={{ width: '30rem' }}>
              <p className={styles.titleBig}>{t('pop_up.leftPartTitle.claim')}</p>
              <p className={styles.dataGreen}>{earnedCount.toFixed(2)} MNDCC</p>
            </div>
          )}
        </div>

        {popUpType !== 'claim' && (
          <div className={styles.popUpNameWrapper}>
            {popUpType === 'buy' && <p className={styles.popUpName}>{t('pop_up.buy_text')}</p>}
            {popUpType === 'remove' && <p className={styles.popUpName}>{t('pop_up.remove_text')}</p>}
          </div>
        )}

        {popUpType !== 'claim' && (
          <div className={styles.popUpInputWrapper}>
            <input
              autoFocus={true}
              type="number"
              className={styles.popUpInput}
              placeholder={t('pop_up.input_placeholder')}
              onChange={e => onChangeHandler(e)}
            />
          </div>
        )}

        {amount || popUpType === 'claim' ? (
          <div className={styles.buttonBlock}>
            <div className={styles.button} onClick={() => onClickHandler(popUpType)}>
              {popUpType === 'buy'
                ? t('pop_up.button_name.buy')
                : popUpType === 'remove'
                ? t('pop_up.button_name.remove')
                : t('pop_up.button_name.collect')}
            </div>
          </div>
        ) : (
          <div className={styles.buttonBlock}>
            <div className={styles.disabled}>
              {popUpType === 'buy'
                ? t('pop_up.button_name.buy')
                : popUpType === t('pop_up.button_name.remove')
                ? 'WITHDRAW'
                : t('pop_up.button_name.collect')}
            </div>
          </div>
        )}
      </div>
    </section>
  );
};
