import { FC } from 'react';
import { PoolDashboardCard } from '../home_page/lp_pool_dashboard_card/lp_pool_dashboard_card';
import { Layout } from '../layout/layout_parts/layout';
import { PopUp } from '../pop_up/pop_up';
import { Deposits } from './deposits/deposits';
import { StakeTable } from './stake_table/stake_table';
import styles from './staking_page.module.scss';
import Fox from '../../assets/icons/metamask.png';
import { useStakingPage } from './hooks/useStakingPage.hook';
import { PendingPopUp } from '../pop_up/pending_pop_up';

export const StakingPage: FC = () => {
  const {
    data,
    message,
    isError,
    popUpType,
    isPopUpOpen,
    earnedCount,
    ballanceCount,
    isPendingPopUpOpen,
    contractHash,
    setHash,
    closePopUpPendingStateHandler,
    closePopUpStateHandler,
    popUpStateHandler,
    openPopUpPendingStateHandler,
  } = useStakingPage();

  return (
    <>
      {isPendingPopUpOpen && (
        <>
          <div className={styles.overlay} onClick={closePopUpPendingStateHandler}></div>
          <PendingPopUp
            isPopUpOpen={isPopUpOpen}
            data={data}
            closePopUpStateHandler={closePopUpPendingStateHandler}
            popUpType={popUpType}
            contractHash={contractHash}
          />
        </>
      )}
      {isPopUpOpen && (
        <>
          <div className={styles.overlay} onClick={closePopUpStateHandler}></div>
          <div>
            <PopUp
              isPopUpOpen={isPopUpOpen}
              data={data}
              closePopUpStateHandler={closePopUpStateHandler}
              popUpType={popUpType}
              openPopUpPendingStateHandler={openPopUpPendingStateHandler}
              closePopUpPendingStateHandler={closePopUpPendingStateHandler}
              setHash={setHash}
            />
          </div>
        </>
      )}
      <Layout>
        <section className={styles.wrapper}>
          <PoolDashboardCard staking_page popUpStateHandler={(a: string) => popUpStateHandler(a)} />
          {isError !== message ? (
            <>
              <StakeTable
                data={data}
                popUpStateHandler={(a: string) => popUpStateHandler(a)}
                ballance={ballanceCount}
                earned={earnedCount}
              />
              <Deposits ballance={ballanceCount} popUpStateHandler={(a: string) => popUpStateHandler(a)} />
            </>
          ) : (
            <div className={styles.connectNotification}>
              <img src={Fox} className={styles.icon} alt="icon" />
              <p className={styles.text}>{'Please connect Metamask or any other Web3 Wallet'}</p>
            </div>
          )}
        </section>
      </Layout>
    </>
  );
};
