import { FC } from 'react';
import styles from './stake_table.module.scss';
import cn from 'classnames';
import Icon from '../../../assets/icons/streamline-icon-gift-box@25x25-biege.svg';
import ReactTooltip from 'react-tooltip';
//import RightArrow from '../../../assets/icons/right_arrow_icon.svg';

import { useTranslation } from 'react-i18next';
import { timeConverter } from '../../../helpers/helpers';

const fakeData = [
  { name: 'Mondo Staking', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
  { name: 'Mondo Staking', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
  { name: 'Mondo Staking', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
  { name: 'Mondo Staking', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
];
interface Props {
  data: any;
  popUpStateHandler?: (a: string) => void;
  earned: number;
  ballance: number;
}

export const StakeTable: FC<Props> = ({ popUpStateHandler, earned, ballance }) => {
  const { t } = useTranslation();

  return (
    <section className={styles.wrapper}>
      <div className={styles.dataBlock}>
        <div className={styles.dataWrapper} style={{ width: '20rem' }}>
          <p className={styles.title}>{t('staking-page.stake-table-data.name')}</p>
        </div>
        <div className={styles.dataWrapper} style={{ width: '50rem' }}>
          <p className={styles.title}>{t('staking-page.stake-table-data.Anteil')}:</p>
          <p className={styles.title}>{ballance.toFixed(2)} MNDCC</p>
          <p className={styles.percent}>{'(10,07%)'}</p>
        </div>
        <div className={styles.dataWrapper} style={{ width: '55rem' }}>
          <p className={styles.title}>{t('staking-page.stake-table-data.Reward Konto')}:</p>
          <p className={styles.greenTitle}>{earned.toFixed(2)} MNDCC</p>
        </div>
        <div className={styles.dataWrapper} style={{ width: '30rem' }}>
          <p className={styles.title}>{t('staking-page.stake-table-data.apy')}:</p>
          <p className={styles.greenTitle}>25,89%</p>
        </div>
      </div>
      <div className={styles.tableTitles}>
        <div className={styles.tableTitle} style={{ width: '31rem' }}>
          <p style={{ marginLeft: '2rem', marginBottom: 0 }}>{t('staking-page.stake-table-titles.Pool')}</p>
        </div>
        <div className={styles.tableTitle} style={{ width: '15.5rem' }}>
          {t('staking-page.stake-table-data.Anteil')}
        </div>
        <div className={styles.tableTitle} style={{ width: '32rem' }}>
          {t('staking-page.stake-table-titles.MNDCC')}
        </div>
        <div className={styles.tableTitle} style={{ width: '43rem' }}>
          {t('staking-page.stake-table-titles.Wert')} <span style={{ color: '#9A9A9A' }}>USD</span>
        </div>
        {/* <div className={styles.tableTitle} style={{ width: '21rem' }}>
          {t('staking-page.stake-table-titles.Anzahl Rewards')}
        </div> */}
        <div className={styles.tableTitle} style={{ width: '10rem' }}>
          {t('staking-page.stake-table-titles.Aktionen')}
        </div>
      </div>
      {ballance !== 0 && (
        <div className={styles.dataContainer}>
          <div className={styles.tableTitle} style={{ width: '33.5rem' }}>
            Mondo Staking
          </div>
          <div className={styles.tableTitle} style={{ width: '21rem' }}>
            10,07%
          </div>
          <div className={styles.tableTitle} style={{ width: '37rem' }}>
            {ballance.toFixed(5)}
          </div>
          <div className={styles.tableTitle} style={{ width: '46.3rem' }}>
            180,88 $
          </div>
          {/* <div className={styles.tableTitle} style={{ width: '24.5rem' }}>
          {'37'}
          <img
            src={RightArrow}
            alt="arrow"
            className={cn(styles.arrow, { [styles.arrowRotated]: isDataShown })}
            onClick={dataToggle}
          />
        </div> */}
          <div className={styles.tableTitle} style={{ width: '15rem', display: 'flex' }}>
            <div
              data-tip
              data-for="buy"
              className={styles.iconWrapper}
              style={{ backgroundColor: '#64A640' }}
              onClick={() => popUpStateHandler!('buy')}
            >
              <p>+</p>
            </div>
            <div
              data-tip
              data-for="remove"
              className={styles.iconWrapper}
              style={{ backgroundColor: '#BE2A2A' }}
              onClick={() => popUpStateHandler!('remove')}
            >
              <p>-</p>
            </div>
            <div
              data-tip
              data-for="claim"
              className={styles.iconWrapper}
              style={{ backgroundColor: '#AB9B8C' }}
              onClick={() => popUpStateHandler!('claim')}
            >
              <img src={Icon} alt="icon" className={styles.icon} />
            </div>

            <ReactTooltip id="buy" place="top" effect="solid">
              {t('tooltip.buy')}
            </ReactTooltip>
            <ReactTooltip id="remove" place="top" effect="solid">
              {t('tooltip.remove')}
            </ReactTooltip>
            <ReactTooltip id="claim" place="top" effect="solid">
              {t('tooltip.collect')}
            </ReactTooltip>
          </div>
        </div>
      )}

      {ballance === 0 && (
        <div className={styles.buttonBlock}>
          <div className={styles.button} onClick={() => popUpStateHandler!('buy')}>
            {'buy stake'}
          </div>
        </div>
      )}
      <div className={cn(styles.dataHidden)}>
        {fakeData.map((data, i) => (
          <div className={cn(styles.mappedDataContainer)} key={i}>
            <p className={styles.dataItem} style={{ width: '54.5rem' }}>
              {data.name}
            </p>
            <p className={styles.dataItem} style={{ width: '37rem' }}>
              +{data.percent}
            </p>
            <p className={styles.dataItem} style={{ width: '42rem' }}>
              +{data.value}
            </p>
            <p className={styles.dataItem}>{'Auszahlung: ' + timeConverter(data.date)}</p>
          </div>
        ))}
      </div>
    </section>
  );
};
